
import usei18n from '../../../composables/usei18n';
import {formatDate, parseDate, freshDate} from '../../../utils/date';
import {defineComponent, reactive, toRefs} from 'vue';
import Icon from '@ohrm/oxd/core/components/Icon/Icon.vue';
import Input from '@ohrm/oxd/core/components/Input/Input.vue';
import Calendar from '@ohrm/oxd/core/components/Calendar/Calendar.vue';

export default defineComponent({
  name: 'oxd-date-input',
  inheritAttrs: false,

  components: {
    'oxd-icon': Icon,
    'oxd-input': Input,
    'oxd-calendar': Calendar,
  },

  props: {
    modelValue: {
      type: String,
      default: '',
    },
    hasError: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    placeholder: {
      type: String,
      required: false,
    },
    ioformat: {
      type: String,
      default: 'yyyy-MM-dd',
    },
    displayFormat: {
      type: String,
      default: null,
    },
    value: {
      type: String,
      default: null,
    },
  },

  setup(props) {
    const state = reactive({
      open: false,
      dateProxy: parseDate(props.modelValue, props.ioformat),
      dateTyped: '',
    });

    return {
      ...usei18n(),
      ...toRefs(state),
    };
  },

  methods: {
    onBlur(e: Event) {
      if (this.dateTyped) {
        const format =
          this.displayFormat && this.displayFormat.trim() !== ''
            ? this.displayFormat
            : this.ioformat;
        const parsedDate = parseDate(this.dateTyped, format);
        this.dateTyped = '';
        this.dateSelected = parsedDate;
      }
      this.closeDropdown();
      e.stopImmediatePropagation();
      this.$emit('blur');
    },
    onDateTyped(value: string) {
      this.dateTyped = value ? value : ' ';
    },
    onDateSelected() {
      this.closeDropdown();
    },
    toggleDropdown() {
      if (!this.disabled && !this.readonly) {
        if (!this.open) {
          this.$refs.oxdInput.$el.focus();
          this.openDropdown();
        } else {
          this.closeDropdown();
        }
      }
    },
    openDropdown() {
      this.open = true;
      this.$emit('dateselect:opened');
    },
    closeDropdown() {
      this.open = false;
      this.$emit('dateselect:closed');
    },
    onClickToday() {
      this.dateSelected = freshDate();
      this.open = false;
    },
    onClickClear() {
      this.dateSelected = null;
      this.open = false;
    },
  },

  computed: {
    dateSelected: {
      get() {
        return parseDate(this.modelValue, this.ioformat);
      },
      set(value) {
        this.dateProxy = value;
        this.$emit('update:modelValue', formatDate(value, this.ioformat));
      },
    },
    displayDate(): string {
      if (this.value !== null) {
        return this.value;
      } else if (this.displayFormat && this.displayFormat.trim() !== '') {
        return formatDate(this.dateSelected, this.displayFormat);
      } else {
        return formatDate(this.dateSelected, this.ioformat);
      }
    },
    dateIconClasses(): object {
      return {
        'oxd-date-input-icon': true,
        '--disabled': this.disabled,
        '--readonly': this.readonly,
      };
    },
  },
});
